/**
 * 
 * @param  {...any} args - 
 * @returns {Uint8Array} - 
 */
export function concat(...args) {
  //  join 
  return new TextEncoder().encode(args.join(''))
}

// 
export function cloneObject(object) {
  return structuredClone(object)
}

/**
 *  Uint8Array  Uint8Array
 * @param {Uint8Array[]} arrays -  Uint8Array 
 * @returns {Uint8Array}  Uint8Array
 */
export function composite(...args) {
  // 
  const totalLength = args.reduce((acc, arr) => acc + arr.length, 0)

  // 
  const result = new Uint8Array(totalLength)
  let offset = 0
  for (const arg of args) {
    result.set(arg, offset)
    offset += arg.length
  }

  return result
}

// keyvaluenullundefinedkey
export function sortKeys(obj) {
  if (Array.isArray(obj)) {
    return obj.map(sortKeys)
  } else if (typeof obj === 'object' && obj !== null) {
    return Object.keys(obj).sort().reduce((result, key) => {
      result[key] = sortKeys(obj[key])
      return result
    }, {})
  } else if (obj === null) {
    return undefined
  }
  return obj
}